* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }


  .back-button-container {
    padding: 15px 20px 0;
  }
  
  .back-button {
    background-color: #f1f1f1;
    border: none;
    border-radius: 4px;
    padding: 8px 16px;
    font-size: 14px;
    color: #333;
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    transition: all 0.2s ease;
  }
  
  .back-button:hover {
    background-color: #e0e0e0;
  }
  
  
  .project-details-wrapper {
    width: 95.8vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #f5f5f5;
  }

  .loading-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh; 
    background-color: #f5f5f5; 
  }
  
  .project-details-container {
    flex: 1;
    display: flex;
    padding: 0 0px 20px 0px;
  }
  
  .project-details-grid {
    width: 100%;
    display: flex;
    height: 100%;
  }
  
  .project-left-col {
    width: 30%;
    display: grid;
    grid-template-rows: auto auto 1fr;
    gap: 20px;
    padding: 20px;
    padding-right: 10px;
  }
  
  .project-right-col {
    width: 70%;
    display: grid;
    grid-template-rows: 1fr auto;
    gap: 20px;
    padding: 20px;
    padding-left: 10px;
  }
  
  .project-description-container,
  .project-overview-container,
  .project-users-container,
  .project-tasks-container,
  .project-timeline-container {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.15);
    padding: 20px;
    overflow: auto;
  }
  
  .project-overview-container {
    min-height: 150px;
  }
  
  /* Estilos responsive */
  @media screen and (max-width: 1200px) {
    .project-details-grid {
      flex-direction: column;
    }
    
    .project-left-col,
    .project-right-col {
      width: 100%;
      padding: 10px;
    }
    
    .project-left-col {
      grid-template-rows: auto auto auto;
    }
  }
  
  @media screen and (max-width: 768px) {
    .project-details-wrapper {
      width: 100vw;
    }
    
    .project-overview-cards {
      flex-direction: column;
    }
    
    .project-tasks-table-container {
      overflow-x: auto;
    }
  }